let state = {
	currentYear: null,
	list: []
}

let mutations = {
	setYear: (state, payload) => {
		state.currentYear = payload
		let list = JSON.parse(window.sessionStorage.getItem('list'))
		state.list = list[payload]
	}
}

let actions = {

}

export default {
	namespaced:true,
	state,
	mutations,
	actions
}


